/*BURGER MENU*/

@media (min-width: 480px) {
    .HamburgerMenu_hamburgerMenu__wtOeT {
       display: none;
    }
}

/* скрываем чекбокс */
.HamburgerMenu_menu__toggle__gy_Hs {
    opacity: 0;
}
/* стилизуем кнопку */
.HamburgerMenu_menu__btn__3135c {
    display: flex; /* используем flex для центрирования содержимого */
    align-items: center;  /* центрируем содержимое кнопки */
    position: fixed;
    top: 20px;
    left: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 3;
}
/* добавляем "гамбургер" */
.HamburgerMenu_menu__btn__3135c > span,
.HamburgerMenu_menu__btn__3135c > span::before,
.HamburgerMenu_menu__btn__3135c > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #616161;
}

.HamburgerMenu_menu__btn__3135c > span::before {
    content: '';
    top: -8px;
}

.HamburgerMenu_menu__btn__3135c > span::after {
    content: '';
    top: 8px;
}

/* контейнер меню */
.HamburgerMenu_menu__box__1U9aV {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: -100%;
    min-width: 320px;
    height: 100%;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    text-align: center;
    background-color: #ECEFF1;
    box-shadow: 1px 0 6px rgba(0, 0, 0, .2);
}
/* элементы меню */
.HamburgerMenu_menu__item__12hTd {
    display: inline-block;
    padding: 12px 24px;
    width: 320px;
    color: #333;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
    margin: 0 auto;
}
.HamburgerMenu_menu__item__12hTd:hover {
    background-color: #CFD8DC;
}

.HamburgerMenu_menu__toggle__gy_Hs:checked ~ .HamburgerMenu_menu__btn__3135c > span {
    transform: rotate(45deg);
}
.HamburgerMenu_menu__toggle__gy_Hs:checked ~ .HamburgerMenu_menu__btn__3135c > span::before {
    top: 0;
    transform: rotate(0);
}
.HamburgerMenu_menu__toggle__gy_Hs:checked ~ .HamburgerMenu_menu__btn__3135c > span::after {
    top: 0;
    transform: rotate(90deg);
}
.HamburgerMenu_menu__toggle__gy_Hs:checked ~ .HamburgerMenu_menu__box__1U9aV {
    visibility: visible;
    left: 0;
    z-index: 2;
}

.HamburgerMenu_menu__btn__3135c > span,
.HamburgerMenu_menu__btn__3135c > span::before,
.HamburgerMenu_menu__btn__3135c > span::after {
    transition-duration: .25s;
}
.HamburgerMenu_menu__box__1U9aV {
    transition-duration: .25s;
}
.HamburgerMenu_menu__item__12hTd {
    transition-duration: .25s;
}

